import React, { PureComponent } from 'react'
import ReactMarkdown from 'react-markdown'
import { Follow } from "react-twitter-widgets";
import {Helmet} from "react-helmet"

export default class About extends PureComponent {
    componentDidMount () {
        window.scrollTo(0,0)
    }

    // Helper method
    // LinkRenderer = (props) => <a href={props.href} target="_blank" rel="noopener noreferrer">{props.children}</a>
    
    render () {
        const {lang, infoPic, about} = this.props
        const isEn = lang==="en"
        const {seoTitle, seoTitleCN, name, nameCN, details, detailsCN} = about
        const detailsContent = details.map((detail, idx) => 
            <ReactMarkdown
                className='detail-info bigger-first-letter'
                key={`detail${idx}`}
                children={detail}
                // renderers={{link: this.LinkRenderer}}
                linkTarget="_blank"
            />
        )
        const detailsContentCN = detailsCN.map((detail, idx) =>
            <ReactMarkdown
                className='detail-info bigger-first-letter'
                key={`detail${idx}`}
                children={detail}
                // renderers={{link: this.LinkRenderer}}
                linkTarget="_blank"
            />
        )

        return (
            <div id='maincontent-container'>
                <main className='about'>
                    <Helmet>
                        <title>{ isEn ? seoTitle : seoTitleCN }</title>
                        <meta name="description" content={ isEn? details.join(" ") : detailsCN.join(" ") } />
                    </Helmet>
                    <div className='info'>
                        <h2>{isEn ? `About ${name}` : nameCN }</h2>
                        <div className='pic-container'>
                            <img className='info-pic' src={infoPic} alt={name} />
                        </div>
                        <div>
                            { isEn ? detailsContent : detailsContentCN}
                        </div>
                        <div className='clear'/>
                        {/* <div className='clear'></div> */}
                    </div>
                    <div className='contact'>
                        <h2>{ isEn ? "Contact Info" : "联系方式"}</h2>
                        <address>
                            <p>Rutgers University</p>
                            <p>64 College Ave<br/>New Brunswick<br/>NJ 08901<br/>United States</p>
                            <p><a href='mailto:tjiang@rutgers.edu'>tjiang@rutgers.edu</a></p>
                            <Follow username="TaoJiangScholar" options={{ dnt: true, showCount: false, size: "large" }}/>
                        </address>
                    </div>
                </main>
            </div>
        )
    }
}